

import { Direction, Directionality } from '@angular/cdk/bidi';
import { BooleanInput } from '@angular/cdk/coercion';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnDestroy,
  OnInit,
  Optional,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { TDSConfigKey, TDSConfigService, WithConfig } from 'tds-ui/core/config';
import { InputBoolean } from 'tds-ui/shared/utility';

import { TDSCollapsePanelComponent } from './collapse-panel.component';

const TDS_CONFIG_MODULE_NAME: TDSConfigKey = 'collapse';

@Component({
  selector: 'tds-collapse',
  exportAs: 'tdsCollapse',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  template: `
    <ng-content></ng-content>
  `,
  host: {
    "class":"block box-border m-0 p-0 rounded",
    '[class.tds-collapse-icon-position-left]': `expandIconPosition === 'left'`,
    '[class.tds-collapse-icon-position-right]': `expandIconPosition === 'right'`,
    '[class.tds-collapse-ghost]': `ghost`,
    '[class.tds-collapse-borderless]': '!bordered',
    '[class.tds-collapse-rtl]': "dir === 'rtl'",
    '[class.border]': 'bordered',
    '[class.border-neutral-2-200]': 'bordered',
    '[class.dark:border-d-neutral-2-200]': 'bordered',
    '[class.border-b-0]': 'bordered',
  }
})
export class TDSCollapseComponent implements OnDestroy, OnInit {
  readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;
  static ngAcceptInputType_accordion: BooleanInput;
  static ngAcceptInputType_bordered: BooleanInput;
  static ngAcceptInputType_ghost: BooleanInput;

  @Input() @WithConfig() @InputBoolean() accordion: boolean = false;
  @Input() @WithConfig() @InputBoolean() bordered: boolean = true;
  @Input() @WithConfig() @InputBoolean() ghost: boolean = false;
  @Input() expandIconPosition: 'left' | 'right' = 'left';
 
  dir: Direction = 'ltr';

  private listOfTDSCollapsePanelComponent: TDSCollapsePanelComponent[] = [];
  private destroy$ = new Subject<void>();
  constructor(
    public tdsConfigService: TDSConfigService,
    private cdr: ChangeDetectorRef,
    private elementRef: ElementRef,
    @Optional() private directionality: Directionality
  ) {
    // TODO: move to host after View Engine deprecation
    this.elementRef.nativeElement.classList.add('tds-collapse');

    this.tdsConfigService
      .getConfigChangeEventForComponent(TDS_CONFIG_MODULE_NAME)
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        this.cdr.markForCheck();
      });
  }

  ngOnInit(): void {
    this.directionality.change?.pipe(takeUntil(this.destroy$)).subscribe((direction: Direction) => {
      this.dir = direction;
      this.cdr.detectChanges();
    });

    this.dir = this.directionality.value;
  }

  addPanel(value: TDSCollapsePanelComponent): void {
    this.listOfTDSCollapsePanelComponent.push(value);
  }

  removePanel(value: TDSCollapsePanelComponent): void {
    this.listOfTDSCollapsePanelComponent.splice(this.listOfTDSCollapsePanelComponent.indexOf(value), 1);
  }

  click(collapse: TDSCollapsePanelComponent): void {
    if (this.accordion && !collapse.active) {
      this.listOfTDSCollapsePanelComponent
        .filter(item => item !== collapse)
        .forEach(item => {
          if (item.active) {
            item.active = false;
            item.activeChange.emit(item.active);
            item.markForCheck();
          }
        });
    }
    collapse.active = !collapse.active;
    collapse.activeChange.emit(collapse.active);
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
